export default {
	data() {
		return {
			url:['/pages/index/index','/pages/order/index','/pages/order/category','/pages/address/index','/pages/my/index']
		}
	},
	props: {
		tarbar: {
			type: Number,
			default: 5
		},
	},
	onLoad() {

	},
	methods: {
		toPage(url) {
			uni.redirectTo({
				url
			})
		},
		toIndex(index) {
			if(index!=this.tarbar){
				this.toPage(this.url[index]);
			}
			
			//this.$emit('toIndex', index)
			//this.home = index
		}
	}
}
